<template>
  <div class="slider-container">
    <div class="slider-track" :style="{ transform: `translateY(-${currentIndex * itemHeight}px)`, transition: transitioning ? 'transform 1s ease' : 'none' }">
      <div
        class="text-slide"
        v-for="(text, index) in duplicatedTexts"
        :key="index"
      >
        {{ text }}
      </div>
    </div>
  </div>
</template>
<script setup>
import { ref, onMounted } from 'vue'
const texts = [
  'ÌÉÄÏäà ÏûêÏ†ÑÍ±∞ Ï†ïÎ≥¥ÏôÄ Ïö¥ÎèôÏãúÍ∞ÑÏùÑ Ìïú Î≤àÏóê Î≥º Ïàò ÏûàÏäµÎãàÎã§.',
  'ÌÉÄÏù¥Î®∏ Î≤ÑÌäº ÏïàÏóê ÏπºÎ°úÎ¶¨ Í≥ÑÏÇ∞Í∏∞Í∞Ä Îì§Ïñ¥Í∞Ä ÏûàÏäµÎãàÎã§.',
  'ÏπºÎ°úÎ¶¨ Í≥ÑÏÇ∞Í∏∞Î•º Ïù¥Ïö©ÌïòÎ©¥ Ïàú ÏπºÎ°úÎ¶¨ÏñëÏùÑ Í≥ÑÏÇ∞Ìï¥ Î≥¥ÏÑ∏Ïöî.',
  'Ïù¥ Ïï±ÏùÄ ÌÉÄÏäà Ï†ïÎ•òÏû•ÏùÑ Í∏∞Ï§ÄÏúºÎ°ú ÌïòÍ≥† ÏûàÏäµÎãàÎã§.',
]
const duplicatedTexts = [...texts, ...texts] // Î¨¥Ìïú Î£®ÌîÑ ÏúÑÌï¥ Î≥µÏ†ú
const currentIndex = ref(0)
const itemHeight = 22 // Ìïú Ï§Ñ ÎÜíÏù¥(px)
const transitioning = ref(true)
const startSlider = () => {
  const loop = () => {
    transitioning.value = true
    currentIndex.value++
    setTimeout(() => {
      transitioning.value = false
      // Î¨¥Ìïú Î£®ÌîÑ Ï≤òÎ¶¨
      if (currentIndex.value >= texts.length) {
        currentIndex.value = 0
      }
      setTimeout(loop, 4000) // Î©àÏ∂îÎäî ÏãúÍ∞Ñ
    }, 1000) // Ïä¨ÎùºÏù¥Îìú Ïù¥Îèô ÏãúÍ∞Ñ
  }
  loop()
}
onMounted(() => {
  startSlider()
})
</script>
<style scoped>
.slider-container {
  height:22px;
  overflow: hidden;
  border: 1px solid #ccc;
  position: relative;
  border-radius: 35px;
  margin-top: 12px;
}
.slider-track {
  display: flex;
  flex-direction: column;
}
.text-slide {
  padding-left:15px;
  height: 22px;
  line-height: 22px;
  font-size: 12px;
  background: #f8f8f8;
}
</style>
